import { Component, Directive, EventEmitter, Input, Output } from '@angular/core';
import { NotificationStreamItem } from '@backbase/notifications-common-ang';
import { SeverityLevel } from '@backbase/data-ang/notifications';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { parseNotificationSeverityLevel } from '../../helpers/mappers';
import { NotificationSeverityType } from '../../models/notification-severity-type.model';

@Component({
  selector: 'bb-notifications-sticky-list-item',
  templateUrl: './notifications-sticky-list-item.component.html',
})
export class NotificationsStickyListItemComponent {
  /**
   * Notification stream item
   */
  @Input() notification?: NotificationStreamItem;
  /**
   * Notification stream item close event
   * Emits notification stream item id
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() close: EventEmitter<NotificationStreamItem | string> = new EventEmitter();

  /**
   * Method to handle close event from alert component
   *
   * @param notification Notification data
   */
  onClose(notification: NotificationStreamItem | string) {
    this.close.emit(notification);
  }

  /**
   * Method to parse severity level into alert modifier
   *
   * @param level Severity level
   * @returns Notification severity level type
   */
  parseNotificationSeverityLevel(level: SeverityLevel): NotificationSeverityType {
    return parseNotificationSeverityLevel(level);
  }
}

@Directive({
  selector: '[bbNotificationsStickyListItemCustomizable]',
})
export class NotificationsStickyListItemCustomizableDirective extends BbTemplate<
  NotificationsStickyListItemComponent,
  NotificationStreamItem | undefined
> {}
